<template>
  <div class="review-wrapper">
      <teach></teach>
      <p>You are reviewing XXXXX</p>

<yd-cell-group title="打几分">
   
      <el-slider v-model="reviewpoint"></el-slider>   
</yd-cell-group>

    <yd-cell-group title="你觉得这个老师专业吗？">
        <yd-cell-item arrow type="label">
            <select slot="right">
                <option value="">请慎重选择，因为会影响你的公信力</option>
                <option value="1">非常专业</option>
                <option value="2">还行</option>
                <option value="3">马马虎虎</option>
                <option value="4">不靠谱</option>
            </select>
        </yd-cell-item>
    </yd-cell-group>




    <yd-cell-group title="写一些文字评论">
        <yd-cell-item>
            <yd-textarea v-model="desc" slot="right" placeholder="" maxlength="100"></yd-textarea>
        </yd-cell-item>
    </yd-cell-group>

  <yd-cell-group title="还有什么你需要补充的？（可以多选）">
        <yd-cell-item type="checkbox">
            <span slot="left">老师很幽默</span>
            <input slot="right" type="checkbox" value="Han MeiMei" v-model="checkedNames"/>
        </yd-cell-item>

        <yd-cell-item type="checkbox">
            <span slot="left">老师很帅气</span>
            <input slot="right" type="checkbox" value="Li Lei" v-model="checkedNames"/>
        </yd-cell-item>

        <yd-cell-item type="checkbox">
            <span slot="left">老是很帅气</span>
            <input slot="right" type="checkbox" value="Jim Green" v-model="checkedNames"/>
        </yd-cell-item>

        <yd-cell-item type="checkbox">
            <span slot="left">师父声音很好听</span>
            <input slot="right" type="checkbox" value="Jim Green" v-model="checkedNames"/>
        </yd-cell-item>

        <yd-cell-item type="checkbox">
            <span slot="left">师父给我一种信任感</span>
            <input slot="right" type="checkbox" value="Jim Green" v-model="checkedNames"/>
        </yd-cell-item>

        <yd-cell-item type="checkbox">
            <span slot="left">师傅学识很渊博</span>
            <input slot="right" type="checkbox" value="Jim Green" v-model="checkedNames"/>
        </yd-cell-item>

  </yd-cell-group>
  </div>
</template>
<script>
import PrograssBar from '@/components/progress'
import teach from '@/page/teach/teach'
export default {
  components:{
    teach,
    PrograssBar
  },
  data(){
    return {
      pro:'',
      checkedNames:[],
      desc:'',
      reviewpoint:0
    }
  },

}
</script>




