<template>
  <div class="teach-wrapper">
        <yd-preview :buttons="btns">
            <yd-preview-header>
                <div slot="left">Required</div>
                <div slot="right">🐷 + 🐂</div>
            </yd-preview-header>

            <yd-preview-item>
                <div slot="left">Project Name</div>
                <div slot="right">Making apps</div>
            </yd-preview-item>
            <yd-preview-item>
                <div slot="left">Current status</div>
                <div slot="right">{{statuss  = "waiting for accept"}}</div>
            </yd-preview-item>

            <yd-preview-item>
                <div slot="left">Start time</div>
                <div slot="right">2018/08/01</div>
            </yd-preview-item>

            <yd-preview-item>
                <div slot="left">Project description</div>
                <div slot="right">I need 10000 interns help me making my coffe.</div>
            </yd-preview-item>
        </yd-preview>
  </div>
</template>
<script>
export default {
      data() {
        return {
            statuss:'',
            btns: [
                
                {
                    color: '#F00',
                    text: 'Cancel',
                    // link: {path: '/'}
                    click: () => {
                        this.statuss = "Agreed"
                    }
                }
            ]
        }
    }
}
</script>



