<template>
  <div class="progress-bar" ref="progressBar">
      <div class="bar-inner">
          <div class="progress" ref="prograss"></div>
          <div class="progress-btn-wrapper" ref="prograssBtn">
              <div class="progress-btn"></div>
          </div>
      </div>
  </div>
</template>
<script>
export default {
    props: {
        percent : {
            type: Number,
            default: 0  
        }
    },
    watch : {
        percent(newPercent) {
            if (newPercent >= 0) {
                const barWidth = this.$refs.progressBar.clientWidth - 50
                const offsetWidth = newPercent * barWidth
                this.$refs.progress.style.width = `${offsetWidth}px`

            }
        }
    }
  
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">

  .progress-bar
    height: 30px
    .bar-inner
      position: relative
      top: 13px
      height: 4px
      background: rgba(0, 0, 0, 0.3)
      .progress
        position: absolute
        height: 100%
        background: $color-theme
      .progress-btn-wrapper
        position: absolute
        left: -8px
        top: -13px
        width: 50px
        height: 30px
        background #ef4f4f
        .progress-btn
          position: relative
          top: 7px
          left: 7px
          box-sizing: border-box
          width: 16px
          height: 16px
          border: 3px solid $333
          border-radius: 50%
          background: $000
</style>


